export default {
	data() {
		return {
			arrays: [],
			index:0,
			sessionId:'',
			myData:[],
			id:1
		}
	},
	onLoad(option) {
		this.sessionId = option.sessionId
		this.init()
	},
	methods: {
		// 机构信息
		init(){
			uni.request({
				url:'https://www.nssnail.com/wx/getRedCrossList',
				data:{
					sessionId:this.sessionId
				},
				success: (res) => {
					this.myData = res.data.data
					this.myData.forEach((item)=>{
						this.arrays.push(item.mname)
					})
				}
			})
		},
		
		// 选择机构
		bindPickerChange: function(e) {
			this.index = e.target.value
			this.id = parseInt(this.index)+1
		},
		
		// 点击提交，修改机构
		sure(){
			console.log(this.id)
			console.log(this.sessionId,'sess')
			uni.request({
				url:'https://www.nssnail.com/wx/personal/updateUserRedCross',
				method:'POST',
				data:{
					sessionId:this.sessionId,
					id:this.id
				},
				success: (res) => {
					if(res.data.stat == 1){
						uni.showToast({
							title:res.data.message,
							icon:'success',
							success:()=>{
								setTimeout(function(){
									uni.navigateBack({
										delta:1
									})
								},2000);
							}
						})
					}else if(res.data.stat == 3){
						uni.showModal({
						    title: '提示',
						    content: '登录过期，请重新登录',
						    success: function (res) {
						        if (res.confirm) {
						            uni.navigateTo({
						            	url:'../myInit/myInit'
						            })
						        }
						    }
						});
					}else if(res.data.stat == 0){
						uni.showToast({
							title: res.data.message,
							icon:'none',
							duration:3000
						})
					}
				}
			})
		}
	}
}
